* {
  box-sizing: border-box;
}

a {
  color: $color-primary;
  text-decoration: none;
}

body {
  overflow-y: scroll;

  font-family: $font-regular;
  font-weight: normal;
  line-height: normal;
  -webkit-font-smoothing: antialiased;

  background-color: $color-body;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 20px;
  margin-bottom: 15px;

  color: $color-headers;
  font-family: $font-bold;
  font-weight: normal;
  line-height: normal;
  letter-spacing: 0.3px;
  -webkit-font-smoothing: antialiased;
}

::selection {
  color: $color-headers;
  background: $color-primary;
}

h2 {
  font-size: 24px;
  font-family: $font-black;
}

h3 {
  font-size: 19px;
  line-height: 1.25;
  letter-spacing: 1px;
}

h4 {
  font-size: 15px;
  letter-spacing: 0.3px;
}

ul {
  padding: 0;
  margin: 0;
}

p {
  margin: 5px 0;

  color: $color-text;
  font-size: 14px;
  font-family: $font-medium;
  font-weight: normal;
  line-height: 1.69;
  -webkit-font-smoothing: antialiased;

  a {
    color: $color-text;

    &:hover {
      color: $color-primary;
    }
  }
}

b {
  font-family: $font-black;
}

.notification-counter {
  height: 20px;
  margin-left: 10px;
  padding: 4px 5px;

  color: $color-primary;
  font-size: 13px;
  font-family: $font-bold;
  line-height: 1;

  border-radius: 4px;
  background-color: #f8fafc;
}

.svg-defs {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

.tab {
  display: none;

  &.isActive {
    display: block;
  }
}

.tab-link {
  &.isActive {
  }
}

// v-select style rewrite

.v-select {
  .dropdown-toggle {
    // padding-bottom: 4px !important;

    overflow: hidden;

    border: 1px solid #ebedf3 !important;
  }

  .single .dropdown-toggle {
    max-height: 36px !important;
    padding-bottom: 0 !important;
  }

  .selected-tag {
    overflow: hidden;

    display: inline-block;
    margin: 4px 0 !important;
    max-width: 75% !important;

    color: $color-headers;
    font-size: 13px;
    font-family: $font-bold;

    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .form-control {
    padding: 0;
    width: 0 !important;
  }

  &.open .form-control {
    width: 10em !important;
    padding: 0 0.5em;
  }

  .dropdown-menu {
    border: 1px solid #ebedf3 !important;
    border-top: 0 !important;

    li {
      color: $color-headers !important;
      font-size: 13px !important;
      font-family: $font-bold !important;

      a {
        white-space: normal !important;
      }
    }
  }

  .dropdown-toggle .clear {
    font-size: 20px !important;
  }
}

.page-navigation__link {
  position: fixed;
  top: 90px;
  left: 30px;

  display: block;
  font-size: (12/16)*1em;
  color: $color-headers;

  padding-left: 25px;
  overflow: hidden;

  svg {
    position: absolute;
    left: 5px;
    top: calc(50% - 5px);

    width: 15px;
    height: 10px;

    transition: all .4s $default-timing;
  }

  &:hover {
    svg {
      left: -20px;
    }
  }
}



