.brand {
  display: flex;
  align-items: center;

  &__logo {
    margin-right: 20px;

    svg {
      width: 100%;
      height: 100%;

       fill: url(#default-gradient);
    }
  }

  &__text {
    span {
      display: block;
      color: $color-headers;
      font-family: $font-brand;
      text-transform: uppercase;
    }

    p {
      margin: 0;

      color: $color-text;
      font-size: 13px;
      font-family: $font-regular;
      letter-spacing: 0.3px;
    }
  }

  &--big-center {
    max-width: 315px;
    margin-left: auto;
    margin-right: auto;
  }

  &--big-center &__logo {
    width: 75px;
    height: 75px;
  }

  &--big-center &__text {
    span {
      padding-top: 10px;

      color: $color-headers;
      font-size: 29px;
      line-height: 1;
      letter-spacing: 4.1px;
    }
  }

  &--small {
    display: inline-flex;
  }

  &--small &__logo {
    width: 38px;
    height: 35px;
  }

  &--small &__text {
    span {
      font-size: 19px;
      letter-spacing: 2px;
      line-height: 19px;
    }
  }

  &--auth {
    margin-top: 40px;
    margin-bottom: 40px;
  }
}

@include laptop-m {
  .brand {
    &--big-center {
      margin-top: 60px;
    }
  }
}

@include laptop-xl {
  .brand {
    &--big-center {
      display: block;

      text-align: center;
    }

    &--big-center &__logo {
      width: 127px;
      height: 114px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10px;
    }
  }
}



