.img-thumb-group {
  display: flex;
  align-items: center;

  .img-thumb-wrap {
    position: relative;

    min-width: 40px;
    min-height: 40px;
    width: 40px;
    height: 40px;
    margin-left: -15px;

    &:first-of-type {
      margin-left: 0;
    }

    &__text-dropdown {
      position: absolute;
      top: 100%;
      left: 0;
      z-index: 1;

      display: inline-block;

      opacity: 0;
      transform: translateY(20px);
      visibility: hidden;

      transition: visibility 0.2s ease, opacity 0.2s ease, transform 0.2s ease;
    }

    &__text-dropdown-layout {
      padding: 4px 6px;
      margin-top: 10px;

      background-color: #ebedf3;

      border-radius: 4px;

      span {
        color: $color-headers;
        font-size: 12px;
        white-space: nowrap;
      }
    }

    &:hover .img-thumb-wrap__text-dropdown {
      opacity: 1;
      transform: translateY(0);
      visibility: visible;
    }
  }

  .img-thumb {
    min-width: 40px;
    min-height: 40px;
    width: 40px;
    height: 40px;

    border: 2px solid #ffffff;
    border-radius: 50%;

    transition: all 0.1s $default-timing;

    &:hover {
      border-color: $color-primary;
    }
  }
}

.img-thumb {
  position: relative;
  overflow: hidden;

  border-radius: 4px;

  div {
    width: 100%;
    height: 100%;

    background-color: $color-borders;
    background-size: cover;
    background-position: center;

    transition: all 0.5s $default-timing;
  }

  &--poster {
    min-width: 244px;
    min-height: 344px;
    width: 244px;
    height: 344px;
  }

  &--size {
    &-xs {
      min-width: 40px;
      min-height: 40px;
      width: 40px;
      height: 40px;
    }

    &-s {
      min-width: 45px;
      min-height: 45px;
      width: 45px;
      height: 45px;
    }

    &-sm {
      min-width: 50px;
      min-height: 50px;
      width: 50px;
      height: 50px;
    }

    &-x {
      min-width: 70px;
      min-height: 70px;
      width: 70px;
      height: 70px;
    }

    &-m {
      min-width: 90px;
      min-height: 90px;
      width: 90px;
      height: 90px;
    }

    &-l {
      min-width: 131px;
      min-height: 131px;
      width: 131px;
      height: 131px;
    }

    &-xl {
      min-width: 144px;
      min-height: 144px;
      width: 144px;
      height: 144px;
    }
  }

  &--circle {
    border-radius: 50%;
  }
}



