.toolbox {
}

.toolbox-links {
  margin-bottom: 10px;
  margin-left: -5px;

  font-size: 0;
}

.toolbox-toggle {
  position: relative;
}

.toolbox {
  position: absolute;
  top: 100%;
  right: 0;

  opacity: 0;
  transform: translateY(20px);
  visibility: hidden;

  transition: visibility 0.2s ease, opacity 0.2s ease, transform 0.2s ease;

  &.isActive {
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
  }

  &__content {
    position: relative;

    margin-top: 20px;

    border-radius: 12px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
    background-color: #ffffff;

    &:before {
      content: '';

      width: 0;
      height: 0;
      display: block;

      position: absolute;
      top: 0;

      transform: translateY(-100%);

      border: 10px solid transparent;
      border-bottom-color: #ffffff;
    }
  }

  &--header-user-link &__content {
    &:before {
      right: 57px;
    }
  }

  &__layout {
    min-width: 160px;

    border-bottom: 1px solid $color-borders;

    &:last-of-type {
      border-bottom: 0;
    }
  }

  &__list {
    padding: 12px 25px;
    list-style: none;
  }

  &__list-item {
    padding: 8px 0;

    font-size: 13px;

    .version {
      font-size: 12px;
      color: #aaa;
    }
  }

  &__link {
    display: flex;
    align-items: center;

    svg {
      width: 17px;
      height: 17px;
      margin-right: 15px;

      fill: $color-text;
      transition: fill 0.1s ease;
    }

    span {
      color: $color-text;
      transition: color 0.1s ease;
    }

    &:hover {
      span {
        color: $color-primary;
      }
    }
  }
}

.toolbox-link {
  display: inline-flex;
  align-items: center;
  padding: 5px 15px;
  margin-bottom: 5px;
  margin-left: 5px;

  border-radius: 50px;
  border: 1px solid $color-borders;

  label {
    margin: 0;
    margin-right: 10px;

    font-family: $font-black;
    font-size: 12px;
    letter-spacing: 0.2px;
  }

  span {
    color: $color-text;
    font-size: 12px;
    font-family: $font-medium;
  }

  button {
    width: 15px;
    height: 15px;
    padding: 0;
    margin-right: -10px;

    border: 0;
    border-radius: 50%;

    transition: all 0.4s $default-timing;

    cursor: pointer;

    &:hover {
      background-color: $color-primary;
    }

    svg {
      min-width: 5px;
      min-height: 5px;
      width: 5px;
      height: 5px;

      fill: #ffffff;
    }
  }
}

@include laptop-m {
  .toolbox-link {
    margin-bottom: 15px;
    margin-right: 15px;
  }
}



