.preloader {
  min-height: 150px;
  position: relative;
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;

    width: 100%;
    height: 100%;
    transition: all 0.5s $default-timing;

    background-color: rgba(255, 255, 255, 0.95);
  }

  &:before {
    content: '';

    position: absolute;
    top: calc(50% - 18px);
    left: calc(50% - 18px);
    z-index: 101;

    width: 36px;
    height: 36px;
    display: block;

    border: 3px solid transparent;
    border-radius: 50%;
    border-top-color: $color-primary;
    border-left-color: $color-primary;

    animation: spinner 0.6s linear infinite;
  }
}



