.nav-icon {
  min-width: 20px;
  width: 20px;
  height: 13px;

  position: relative;
  z-index: 1;

  transition: all 0.3s ease-in-out;

  cursor: pointer;

  span {
    width: 100%;
    height: 3px;

    position: absolute;
    right: 0;

    display: block;
    border-radius: 30px;

    transform-origin: center;
    transition: all 0.3s ease;
    background-color: $color-secondary;

    cursor: pointer;

    &:nth-child(1) {
      top: 0px;
    }
    &:nth-child(2) {
      top: 10px;
    }
  }
