.sidebar {
  ul {
    list-style: none;

    background-color: #ffffff;

    li {
      position: relative;

      box-shadow: 0 0 0 -10px rgba(0, 0, 0, 0);
      transition: all 0.4s $default-timing;

      .notification-counter {
        position: absolute;
        top: calc(50% - 10px);
        right: 18px;
      }

      a {
        display: flex;
        align-items: center;
        padding: 20px;

        svg {
          width: 22px;
          height: 22px;
          min-width: 22px;
          min-height: 22px;

          margin-right: 10px;

          fill: #BFC7D2;

          transition: fill 0.2s;
        }

        span {
          padding-left: 10px;
          color: $color-text;
          font-size: 13px;
          letter-spacing: 0.3px;

          transition: color 0.2s $default-timing;
        }
      }
      &.isActive,
      &:hover {
        box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.03);
        a {
          svg {
            fill: $color-primary;
          }

          span {
            color: $color-primary;
          }
        }
      }
    }
  }

  &__row {
    padding: 20px;

    border-top: 1px solid $color-borders;

    .button {
      width: 100%;

      margin-bottom: 15px;

      justify-content: center;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}



