
.button {
  display: inline-flex;
  align-items: center;

  border-radius: 4px;
  border: 0;
  outline: 0;

  cursor: pointer;

  transform: translate3d(0, 0, 0);
  transition: all 0.85s $default-timing;

  background-color: transparent;

  span {
    color: #ffffff;
    font-size: 13px;
    font-family: $font-bold;
    letter-spacing: 0.3px;
    white-space: nowrap;

    transform: translate3d(0, 0, 0);
    transition: all 0.3s $default-timing;
  }

  &:disabled {
    opacity: 0.5;

    cursor: default;
  }

  &--close {
    position: absolute;
    top: 1em;
    right: 1em;
    
    svg {
      transform: rotate(45deg);
    }
  }

  &--full-width {
    width: 100%;
    justify-content: center;
  }

  &--info-link {
    display: block;
    text-align: center;

    span {
      color: #a6c9f4;
      font-size: 12px;
    }

    &:hover {
      span {
        color: $color-primary;
      }
    }
  }

  &--size {
    &-xs {
      padding: 5px 0;
    }

    &-s {
      height: 32px;
      padding: 0 25px;
    }
  }

  &--transparent {
    height: auto;
    padding: 0;

    svg {
      width: 10px;
      height: 10px;

      fill:#BFC7D2;
    }

    span {
      color: #BFC7D2;
      line-height: 1;
    }

    &:hover {
      svg {
        fill: $color-primary
      }

      span {
        color: $color-primary;
      }
    }
  }

  &--color {
    &-main {
      span {
        color: $color-primary;
      }

      &:hover {
        span {
          color: lighten($color-primary, 5%);
        }
      }
    }

    &-blue-light {
      svg {
        fill: $color-blue-text;
      }
      span {
        color: $color-blue-text;
      }
    }

    &-main-gradient {
      position: relative;
      overflow: hidden;

      color: #ffffff;

      &:after,
      &:before {
        content: '';

        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        border-radius: 4px;

        transition: all 0.7s $default-timing;
      }

      &:after {
        z-index: 1;

        background-size: 100%;
        background-image: $default-gradient;
        background-position: center;
      }

      span {
        z-index: 3;
      }

      &:hover {
        &:after {
          // temp
          background-size: 205%;
        }
      }
    }

    &-main-outline {
      border: 1px solid $color-primary;

      &:hover {
        background-color: rgba(113, 128, 245, 0.27);
      }

      span {
        color: $color-primary;
      }
    }
  }

  &--icon {
    padding-left: 0;
    align-items: center;

    svg {
      z-index: 3;

      width: 20px;
      height: 14px;

      margin-right: 5px;
      margin-left: 10px;

      transition: fill 0.2s;
    }
  }

  &--icon.button--color {
    &-main-gradient {
      fill: #ffffff;
    }

    &-main-outline {
      fill: $color-primary;
    }
  }

  &--header-icon {
    height: 60px;
    padding: 0 25px;

    border-radius: 0;

    transition: all 0.4s $default-timing;

    &:first-of-type {
      margin-left: 20px;
    }

    svg {
      width: 20px;
      height: 20px;

      fill: #BFC7D2;

      transition: all 0.4s $default-timing;
    }

    &:hover {
      background-color: $color-input-fill;

      svg {
        fill: $color-primary;
      }
    }
  }

  &--transparent.button--icon {
    margin-left: -5px;
  }

  &--spinner {
    position: relative;
    svg {
      opacity: 0;
    }

    span {
      // position: relative;

      color: transparent;

      &:before {
        content: '';

        width: 14px;
        height: 14px;

        position: absolute;
        top: calc(50% - 9px);
        left: calc(50% - 9px);

        display: block;

        border: solid 2px transparent;
        border-left-color: $color-primary;
        border-right-color: $color-primary;
        border-radius: 50%;

        transition: all 0.5s cubic-bezier(0.4, 0.26, 0, 0.97);
        animation: spinner 0.45s linear infinite;
      }
    }
  }
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



