.threads {

    &__layout {
        border-radius: 6px;
        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
        background-color: #ffffff;
    }

    &-head {
        display: flex;
        align-items: center;
        justify-content: space-between;

        padding: 10px 35px;
        margin-bottom: 15px;

        &__title {
            font-size: (15/16)*1em;
            font-family: $font-bold;
            letter-spacing: 0.3px;
            margin: 0;
        }
    }

    &-item {
        padding: 25px 35px 20px;
        margin-bottom: 5px;
        
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__author {
            flex: 0 25%;
        }

        .info {
            margin: 0;

            &__text p {
                margin: 0;
            }
        }
    }
}


.thread {
    &-title {
        font-family: $font-medium;
        margin: 0;
        line-height: 19px;
    }

    &-post-info {
        font-size: (12/16)*1em;
        span {
            color: #D0DFF2;
        }
    }

    &-body {
        margin-bottom: 30px;


        .info__list-item {
            padding: 40px;

            p {
                margin: 0;
                margin-bottom: 15px;
                line-height: (26/14)*1em;

                img {
                    max-width: 100%
                }
            }

            .info {
                margin: 0;
                margin-bottom: 15px;
            }
        }

        .thread-message {
            &__date {
                color: #D0DFF2;
                font-size: (12/16)*1em;
                line-height: (26/12)*1em;
                letter-spacing: 0.3px;
                font-family: $font-medium;
            }
            &__title {
                line-height: (26/15)*1em;
            }
        }

        .button--info-link {
            display: inline-block;
        }
    }
}


