.info-notification {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 13px 22px;

  background-color: #F8F9FC;
  border-radius: 6px;

  &__icon {
    width: 22px;
    height: 22px;
    margin-right: 22px;

    svg {
      width: 20px;
      height: 20px;

      fill: #7F8FA4;
    }
  }

  &__desc {
    margin: 0;
    margin-right: 40px;

    font-size: 14px;
    line-height: 22px !important;
    line-height: normal;
  }

  .button {
    min-width: 140px;

    justify-content: center;
  }
}



