.show-more-text {
  overflow: hidden;

  max-height: 65px;

  transition: max-height 0.2s;

  &.is-collapsed {
    max-height: 1000px;
  }
}

.show-more-toggle {
  color: $color-primary;
  font-size: 15px;
  font-family: $font-medium;

  padding: 0;
  border: 0;
  background-color: transparent;
}



