.info {
  position: relative;
  display: flex;
  margin: 15px 0;

  &__small-text {
    color: #D0DFF2;
    font-size: 12px;
  }

  &--separator {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 2px solid $color-borders;
  }

  &--separator &:last-of-type {
    border-bottom: 0;
  }

  &__date-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    &-inner {
      margin-left: 20px;

      h2 {
        margin: 0;
        margin-bottom: 3px;
      }

      .info-location {
        margin: 0;

        line-height: normal;

        &-country {

        } 

        &-place {
          color: $color-primary;
          margin-left: 1em;
          svg
            {
              width: 12px;
              height: 16px;
            }
        }
      }
    }
  }

  &__text {
    text-align: left;

    h2,
    h3,
    h4,
    h5 {
      margin: 0;
    }

    p {
      margin: 0;
    }

    h2 {
      margin-bottom: 5px;

      font-size: 24px;
    }

    h3 {
      margin-bottom: 8px;

      font-size: 19px;
    }

    h5 {
      letter-spacing: 0.3px;
    }

    a {
      color: $color-headers;

      span {
        display: inline-block;
        margin-top: 10px;

        color: $color-blue-text;
        font-size: 12px;
        font-family: $font-medium;
      }
    }

    p {
      margin: 5px 0;
      color: $color-text;
      font-size: 12px;
      font-family: $font-medium;

      svg {
        width: 10px;
        height: 10px;
        margin-right: 5px;

        fill: #979797;
      }
    }

    &--short-info {
      margin-top: 5px;

      p {
        font-size: 16px;
      }
    }
  }

  &--user-list {
    margin-top: 20px;
    padding-bottom: 20px;

    border-bottom: 1px dashed $color-borders;
  }

  &--user-list &__text {

    h3 {
      margin-bottom: 6px;

      font-family: $font-black;
    }

    p {
      margin: 0;

      font-size: 12px;
    }
  }

  &--size-x &__text {
    h5 {
      margin-bottom: 2px;

      font-size: 13px;
      font-family: $font-regular;
    }
  }

  &--profile &__text {
    // max-width: 50%;
  }

  &__text-group {
    display: flex;
    align-items: center;

    p {
      margin: 0;
      margin-right: 30px;

      font-size: 14px;
      line-height: normal;
    }
  }

  &__buttons {
    display: flex;

    .button {
      margin-right: 10px;
    }
  }

  &--size-xl &__buttons {
  }

  &--profile &__buttons {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  &--event &__buttons {
    margin-top: 20px;
    margin-bottom: 15px;
  }

  &__layout {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 10px 0;
    margin-left: 30px;

    &--between {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
  }

  &--event &__layout {
    justify-content: flex-start;
  }

  &--size-x &__layout {
    margin-top: 4px;
    margin-left: 15px;
  }

  &--size-m {
    align-items: center;
  }

  &--size-m &__layout {
    margin-left: 20px;
  }

  &--profile &__layout {
    max-width: 50%;
    margin-left: 45px;
  }

  &--event &__layout {
    max-width: none;
    margin-right: 25px;
  }

  &--user-list &__layout {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 25px;
  }

  &--size-xl {
    position: relative;

    // .info-date {
    //   position: absolute;
    //   top: 0;
    //   right: 0;
    // }
  }

  &--upload-image {
    p {
      color: $color-text;
      font-size: 14px;
      font-family: $font-medium;
    }
  }

  &__more {
    padding: 15px 0;

    a {
      color: #8C9AAD;
    }

    &-center {
      text-align: center;
    }
  }
}

.info-date {
  width: 96px;
  min-width: 96px;
  height: 69px;

  background-color: $color-primary-light;
  border: 1px solid $color-primary;
  border-radius: 5px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  transition: all 0.2s $default-timing;

  cursor: pointer;

  span {
    display: block;

    color: $color-primary;
    font-family: $font-medium;
    text-align: center;

    transition: all 0.2s $default-timing;

    &:nth-child(1) {
      font-size: 16px;
    }

    &:nth-child(2) {
      font-size: 9px;

      text-transform: uppercase;
    }
  }

  .info-date-inner {
    
    display: inline-block;
    width: 100%;
    height: 100%;

  }
  
  .info-date-top {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 7px;

    border-bottom: 1px solid $color-primary;

    span {
      display: block;

      color: $color-primary;
      font-family: $font-medium;
      text-align: center;

      transition: all 0.2s $default-timing;

      &:nth-child(1) {
        margin-bottom: 3px;
        font-size: 16px;
      }

      &:nth-child(2) {
        font-size: 9px;

        text-transform: uppercase;
      }
    }
  }

  .info-date-bottom {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 6px;

    span {
      display: block;

      color: $color-primary;
      font-family: $font-medium;
      font-size: 7px;
      text-align: center;

      transition: all 0.2s $default-timing;
    }
  }

  &:hover {
    background-color: $color-primary;

    .info-date-top {
      border-color: #ffffff;
    }

    span {
      color: #ffffff;
    }
  }
}

.info__list {

  &-item:not(:last-of-type) {
    padding-bottom: 15px;
    border-bottom: 1px solid $border-color;
  }

}

.info-block {

  position: relative;
  display: flex;
  justify-content: flex-start; 

  width: 100%;
  height: 100%;

  &-two {

    &__one {

      flex-basis: 50%;
      box-sizing: border-box;
      padding-right: 15px;
  
      &:last-of-type {
        padding-right: 0;
      }

      .info-inner {
        display: flex;
      }

      .info__layout {
        margin: 0;
      }

      .info__text {
        height: 100%;
        padding-left: 15px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        h3 {
          font-size: 12px;
          font-weight: 700;
          margin-bottom: 4px;
        }

        p {
          font-size: 10px;
          margin-bottom: 0;
        }
        
      }

    }
  }

  &-four {

    &__one {

      flex-basis: 25%;
      box-sizing: border-box;
      padding-right: 10px;
  
      &:last-of-type {
        padding-right: 0;
      }
  
      .info-inner {
        display: flex;
        flex-direction: column;
      }
  
      .info__layout {
        margin-left: 0;
        margin-bottom: 0;
      }

      .info__text {

        h5 {
          font-size: 12px;
        }

        p {
          font-size: 10px;
          margin-bottom: 0;
          line-height: 1.2;
        }
        
      }
  
    }

  }


}

.info-function {

  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);

  a {
    display: flex;

    svg {
      width: 22px;
      height: 22px;
      min-width: 22px;
      min-height: 22px;
      margin-right: 10px;
      fill: #BFC7D2;

      transition: fill 0.2s;
    }

    &:hover svg {
      fill: $color-primary;
    }

  }
}

.info-status {
  position: relative;

  padding-left: 15px;

  color: $color-primary;
  font-size: 14px;
  font-family: $font-medium;

  &:before {
    content: '';

    position: absolute;
    top: 4px;
    left: 0;

    width: 8px;
    height: 8px;

    border-radius: 50%;
    background-color: $color-primary;
  }
}



