@mixin pocket {
  @media screen and (min-width: #{$pocket}) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: #{$tablet}) {
    @content;
  }
}

@mixin laptop-s {
  @media screen and (min-width: #{$laptop-s}) {
    @content;
  }
}

@mixin laptop-m {
  @media screen and (min-width: #{$laptop-m}) {
    @content;
  }
}

@mixin laptop-l {
  @media screen and (min-width: #{$laptop-l}) {
    @content;
  }
}

@mixin laptop-xl {
  @media screen and (min-width: #{$laptop-xl}) {
    @content;
  }
}

// vertical media
@mixin v-pocket {
  @media screen and (min-width: #{$pocket}) and (min-height: $vertical-m) {
    @content;
  }
}

@mixin v-tablet {
  @media screen and (min-width: #{$tablet}) and (min-height: $vertical-m) {
    @content;
  }
}

@mixin v-laptop-s {
  @media screen and (min-width: #{$laptop-s}) and (min-height: $vertical-m) {
    @content;
  }
}

@mixin v-laptop-m {
  @media screen and (min-width: #{$laptop-m}) and (min-height: $vertical-m) {
    @content;
  }
}
