.select2-selection--single {
  .select2-selection__rendered {
    padding: 10px 15px;

    color: $color-text;
    font-size: 13px;
    font-family: $font-bold;
    white-space: nowrap;

    border-radius: 4px;
    border: 1px solid $color-borders;

    background-color: $color-input-fill;
  }

  .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold;
  }

  .select2-selection__placeholder {
    color: #999;
  }

  .select2-selection__arrow {
    position: absolute;

    top: calc(50% - 4px);
    right: 10px;

    width: 13px;
    height: 6px;

    &:after {
      content: '';

      position: absolute;
      top: 0;
      right: 0;

      margin-top: 0px;

      transform: rotate(-45deg) translate3d(0, -50%, 0);
      transform-origin: center;
      transition: all 0.4s cubic-bezier(0.4, 0.26, 0, 0.97);

      width: 8px;
      height: 8px;

      border-left: 2px solid #7f8fa4;
      border-bottom: 2px solid #7f8fa4;
    }
  }
}

&[dir='rtl'] {
  .select2-selection--single {
    .select2-selection__clear {
      float: left;
    }

    .select2-selection__arrow {
      left: 1px;
      right: auto;
    }
  }
}

&.select2-container--disabled {
  .select2-selection--single {
    background-color: #eee;
    cursor: default;

    .select2-selection__clear {
      display: none;
    }
  }
}

&.select2-container--open {
  .select2-selection--single {
    .select2-selection__arrow {
      &:after {
        transform: rotate(135deg);
      }
    }
  }
}



