.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  overflow-y: scroll;

  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  visibility: hidden;

  background-color: rgba(127,143,164,0.3);

  transition: opacity 0.3s, visibility 0.3s;

  hr {
    border-top: 1px dashed #BFC7D2;
  }

  &.is-active {
    opacity: 1;
    visibility: visible;
  }

  &.is-active &__content {
    opacity: 1;
    transform: translateY(0);
    transition-delay: 0.1s;
  }

  &__head-icon {
    width: 40px;
    height: 20px;
    fill: $color-primary;
  }

  &__title {
    margin: 0;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  &__content {
    width: 100%;
    padding: 20px;
    margin: 0 20px;
    position: relative;

    text-align: center;

    opacity: 0;
    transform: translateY(50px);

    transition: opacity 0.3s, transform 0.3s;

    border-radius: 6px;
    background-color: #ffffff;
    box-shadow: 0 3px 45px 0 rgba(51,62,73,0.1);

    p {
      font-size: 16px;
    }
  }

  &__row {
    margin-top: 35px;

    &--between {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
}

.add-user-form {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;

  &__label {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 20px;

    svg {
      width: 18px;
      height: 18px;
      margin-bottom: 4px;

      fill: #BFC7D2;
    }
    span {
    	color: #BFC7D2;
      font-size: 10px;
      line-height: 1;
    }
  }

  .input {

    &--type-text {
      margin-top: 20px;
    }
  }
}

@include laptop-m {
  .modal {

    &__content {
      padding: 50px 55px;
    }
  }
}



