.calendar-controls {
  display: flex;
  align-items: center;
  justify-content: center;


  &__btn {
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;

    transition: opacity 0.2s;

    cursor: pointer;

    svg {
      width: 12px;
      height: 12px;
    }

    &:focus {
      outline: 0;
    }

    &:hover {
      opacity: 0.6;
    }
  }

  &__text {
    margin: 0 10px;

    color: #222C3C;
    font-size: 15px;
    font-family: $font-bold;
  }
}

.calendar {
  margin-bottom: 30px;

  border-radius: 6px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.05);
  background-color: #ffffff;

  &__days-titles {
    display: flex;
    list-style: none;

    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: #F8F9FC;
  }

  &__day-title {
    width: 14.2857142857%;
    padding: 30px 0;
    border-right: 1px solid #EBEEF1;

    text-align: center;
    font-family: $font-bold;
    font-size: 14px;
    text-transform: uppercase;

    &:last-of-type {
      border-right: 0;
    }
  }

  &__days {
    display: flex;
    flex-wrap: wrap;

    list-style: none;
  }

  &__day {
    position: relative;

    width: 14.2857142857%;
    height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    border-right: 1px solid #EBEEF1;
    border-top: 1px solid #EBEEF1;

    transition: background-color 0.2s;

    &:hover {
      background-color: darken(#ffffff, 2.5%);
    }

    &-number {
      position: absolute;
      top: 6px;
      left: 12px;

      color: #7F8FA4;
      font-size: 12px;

      transition: color 0.2s;
    }

    &:hover &-number {
      color: $color-primary;
    }

    &--next-month &-number {
      color: #BFC7D2;
    }

    &--next-month:hover &-number {
      color: darken(#BFC7D2, 2.5%);
    }

    &--next-month:hover {
      background-color: #ffffff;
    }

    &:nth-child(7n) {
      border-right: 0;
    }
  }


  &__day-events {
    display: flex;
    align-items: center;
    justify-content: center;

    &-more {
      display: block;
      margin-top: 10px;
      margin-bottom: -20px;

      color: #A6C9F4;
      font-size: 12px;

      transition: color 0.2s;

      &:hover {
        color: $color-primary;
      }
    }
  }

  &__event-link {
    position: relative;

    width: 30px;
    height: 30px;

    display: block;
    margin: 0 3px;

    border-radius: 5px;

    background-size: cover;
    background-position: center;
  }

  &__event-info {
    position: absolute;
    top: 100%;
    left: calc(50% - 105px);
    z-index: 10;

    transform: translateY(-15px);
    opacity: 0;
    visibility: hidden;

    transition: transform 0.3s, opacity 0.3s, visibility 0.1s;


    &-inner {
      width: 210px;
      margin-top: 10px;

      border-radius: 6px;
      box-shadow: 0 5px 19px 0 rgba(0,0,0,0.07);
    }

    &-content {
      padding: 18px 24px;
      background-color: #ffffff;
    }

    &-img {
      position: relative;
      overflow: hidden;

      width: 100%;
      height: 100px;

      background-position: center;
      background-size: cover;

      &-triangle {
        position: absolute;
        top: 0;
        z-index: 100;
      }


      &-bg {
        position: absolute;
        top: 10px;
        left: 0;

        width: 100%;
        height: 100%;

        background-size: cover;
        background-repeat: no-repeat;
        background-position-y: -10px;

        border-top-right-radius: 6px;
        border-top-left-radius: 6px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 5px;
    }

    &-desc {
      margin: 0;

      font-size: 12px;
    }
  }

  &__event-link:hover &__event-info {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;

    transition-delay: 0.2s;
  }
}



