  position: absolute;
  top: 1px;
  left: 0;
  width: 12px;
  height: 12px;
  transform-origin: center;
  transition: all 0.5s cubic-bezier(0.4, 0.25, 0, 1);
  border-radius: 2px;
  border: 1px solid transparent;
}

.input--type-box label:after {
  transform: scale(1);
  border-color: #ebedf3;
  background-color: #f8fafc;
}

.input--type-box label:before {
  content: '✔';
  left: 1px;
  z-index: 2;
  color: #fc5c7d;
  text-align: center;
  line-height: 14px;
  transform: scale(0);
}

.input--type-box label span {
  display: block;
}

.input--type-box label span:nth-child(1) {
  color: #222c3c;
  font-family: "MuseoSansCyrl-Bold", sans-serif;
}

.input--type-box label span:nth-child(2) {
  color: #7f8fa4;
  font-size: 12px;
}

.input--type-box input[type='radio'] + label:after, .input--type-box input[type='radio'] + label:before {
  border-radius: 50%;
}

.input--type-box input[type='radio'] + label:before {
  content: '';
  width: 12px;
  height: 12px;
  left: 0;
  background-color: #fc5c7d;
}

.input--type-box input:checked + label {
  color: #222c3c;
}

.input--type-box input:checked + label:before {
  transform: scale(1);
}

.input--type-box input[type='radio']:checked + label:before {
  transform: scale(0.5);
}

.input--type-box input:disabled + label {
  color: #c0c7da;
}

.input--type-box input:disabled + label:after, .input--type-box input:disabled + label:before {
  opacity: 0.4;
  filter: grayscale(1);
}

