.profile {
  &__info-header {
    text-align: center;

    .img-thumb {
      margin: 0 auto;
    }
  }

  &__info-header-actions {
    .button {
      width: 100%;
      max-width: 200px;
      margin-bottom: 10px;

      justify-content: center;
    }
  }

  &__info-header-desc {
    h2 {
      margin-top: 10px;
      margin-bottom: 0;

      font-family: $font-black;
    }

    p {
      margin: 0;
      margin-bottom: 15px;

      line-height: 14px;
    }
  }

  &__info-card {
    h4 {
      margin-bottom: 10px;
      margin-top: 30px;
    }

    p {
      margin: 0;
      margin-bottom: 5px;
    }

    .input {
      margin-bottom: 5px;
    }

    &:first-of-type {
      h4 {
        margin-top: 10px;
      }
    }
  }
}

@include tablet {
  .profile {
    &__info-header {
      position: relative;

      display: flex;

      text-align: left;

      .img-thumb {
        margin: 0;
        margin-right: 25px;
      }
    }

    &__info-header-desc {
      h2 {
        margin-top: 5px;
      }

      p {
        margin-top: 3px;
      }
    }

    &__info-header-actions {
      // position: absolute;
      // top: 0;
      // right: 0;

      display: flex;
      align-items: center;

      margin-bottom: 20px;

      .button {
        width: auto;
        max-width: none;
        margin: 0;

        margin-right: 10px;
      }
    }
  }
}

@include laptop-s {
  .profile {
    &__info-header-desc {
      h2 {
        margin-top: 10px;
      }

      p {
        margin-bottom: 20px;
      }
    }

    &__info-header-actions {
      position: absolute;
      top: 10px;
      right: 10px;

      margin-bottom: 0;

      .button {
        margin-right: 0;
        margin-left: 20px;
      }
    }
  }
}

@include laptop-m {
  .profile {
    &__info-header {
      .img-thumb {
        margin-right: 45px;
      }
    }
  }
}



