.page-block-group {
  .page-block {
    margin-bottom: 10px;

    &:last-of-type {
      margin-bottom: 30px;
    }
  }
}

.page-block {
  position: relative;

  display: inline-block;
  width: 100%;
  margin-bottom: 30px;

  border-radius: 6px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
  background-color: #ffffff;

  &__list {
    list-style: none;

    li {
      margin-bottom: 5px;

      color: #7F8FA4;
      font-size: 14px;
      font-family: $font-medium;
      line-height: 26px;

      span {
        font-family: $font-bold;
      }
    }
  }

  &__link-group {
    position: absolute;
    bottom: -5em;

    display: flex;
    flex-direction: column;

    .link {
      font-size: .9em;
      color: rgba($middle, 0.4);
      padding-bottom: .5em;
    }
  }

  &__title-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;

    h2,
    h4 {
      margin: 0;
    }

    .button {
      padding-right: 10px;
    }
  }

  &__float {
    position: absolute;

    .button {
      margin-left: 10px;
    }

    &--top-right {
      top: 30px;
      right: 20px;
    }

    &--bottom-right {
      bottom: 20px;
      right: 20px;
    }
  }

  &__image {
    position: relative;
    overflow: hidden;

    cursor: pointer;

    border-radius: 3px;

    &:before {
      content: '';

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      width: 100%;
      height: 100%;

      background-color: $color-primary;
      opacity: 0;

      transition: opacity 0.2s $default-timing;
    }

    &:hover {
      &:before {
        opacity: 0.2;
      }
    }

    div {
      position: relative;
      overflow: hidden;

      padding-top: 59%;

      background-color: #eeeeee;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
    }
  }

  // 2 columns
  &__masonry {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__masonry-item {
    width: 33.3%;
    p {
      font-size: 12px;
    }
  }

  &__frame {
    margin-top: 20px;
    margin-bottom: 15px;

    background-color: $color-gray-bg;
    border: 1px solid $color-borders;
    border-radius: 4px;

    &--profile-info {
      max-height: 0;
      transition: max-height 0.3s, border 0.3s, margin 0.3s;
      overflow: hidden;

      margin-bottom: 0;
      border-color: transparent;

      .grid__cell {
        margin-bottom: 15px;
      }

      &.isActive {
        max-height: 1000px;
        margin-bottom: 60px;

        border-color: $color-borders;
      }
    }

    &--profile-info .page-block__frame-layout {
      margin-bottom: 0;
    }
  }

  &__frame-layout {
    margin: 30px;
  }

  &__layout {
    margin: 20px;

    p {
      line-height: 1.86;
    }

    &--small {
      margin: 0;
      padding: 5px 20px;
      border-bottom-left-radius: 6px;
      border-bottom-right-radius: 6px;
      background-color: #f9f9fc;

      p {
        margin: 0;

        font-size: 12px;
      }
    }

    &--actions {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &--no-indent {
    overflow: hidden;
  }

  &--no-indent &__layout {
    margin: 0;
  }

  &--new-post {
    padding-top: 10px;
  }

  &__type-message {
    position: relative;

    display: flex;
    align-items: center;

    .input {
      margin-top: 0;
      margin-left: 30px;

      background-color: #ffffff;

      input {
        padding: 9px 15px;
        padding-right: 40px;
      }
    }
  }

  &__action-button {
    position: absolute;
    top: calc(50% - 10px);
    right: 10px;

    padding: 0;

    font-size: 0;

    border: 0;
    background-color: transparent;

    cursor: pointer;

    svg {
      width: 26px;
      height: 21px;

      fill: $color-primary;
    }
  }

  &--search &__layout {
    margin: 0;

    display: flex;
    align-items: center;
    justify-content: center;

    .input {
      border: 0;

      border-radius: 0;

      input {
        padding-top: 22px;
        padding-bottom: 20px;
      }
    }

    .button {
      margin-left: 20px;
      margin-right: 20px;
    }
  }

  &__threads {
    margin: 35px;
  }

  img {
    max-width: 100%;
  }
}

.page-block-header {
  position: relative;

  padding: 0 10px;
  padding-bottom: 15px;

  &__showAll {
    display: flex;
    align-items: center;

    color: $black;
    font-size: 12px;

    svg {
      margin-left: 1em;

      width: 10px;
      height: 8px;

      transform: rotate(180deg);
    }

  }

  .input {
    border-bottom: 0;
  }

  h4,
  h5 {
    font-family: $font-bold;
    letter-spacing: 0.3px;
    margin: 0;
  }

  .input {
    margin-bottom: 5px;
    margin-top: 10px;
  }

  &__layout {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.page-block-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  &__group {

    display: flex;
    align-items: center;
  }

  span {
    display: inline-block;

    color: $color-headers;
    font-size: 12px;
    font-family: $font-medium;
  }

  &__item {
    cursor: pointer;
    transition: all 0.2s $default-timing;

    font-size: 0;

    &:hover {
      transform: translateY(-1px);
    }
  }

  &__item.isActive &__icon,
  &__item:hover &__icon {
    svg {
      fill: $color-primary;
      transition: all 0.2s $default-timing;
    }
  }

  &__icon {
    margin-left: 15px;

    svg {
      width: 15px;
      height: 15px;

      fill: #d9dfe6;
    }
  }
}

.page-sticky {
  position: sticky;
  top: 90px;
}

@include laptop-s {
  .page-block-header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;

    .input {
      max-width: 200px;
    }
  }

}



