.header {
  width: 100%;

  position: fixed;
  z-index: 50;

  transform: translate3d(0, 0, 0);

  background-color: #ffffff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);

  .grid {
    margin: 0;

    &__cell {
      margin: 0;
      padding-left: 0;
    }
  }

  &__content {
    height: 100%;
    height: 60px;

    display: flex;
    align-items: center;
  }

  .brand {
    margin-right: 35px;
  }

  &__nav {
    display: flex;
    justify-content: flex-end;
    list-style: none;

    li {
      &:first-of-type {
        .header__nav-link {
          border-left: 1px solid $color-borders;
        }
      }
    }
  }

  &__nav-link {
    height: 60px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0 25px;

    border-right: 1px solid $color-borders;

    transition: all 0.4s $default-timing;

    cursor: pointer;

    svg {
      width: 20px;
      height: 20px;

      fill: $color-gray-button;

      transition: all 0.4s $default-timing;
    }

    &:hover {
      background-color: $color-input-fill;

      svg {
        fill: $color-primary;
      }
    }
  }

  &__user-link {
    position: relative;

    display: flex;
    align-items: center;
    padding-right: 20px;

    span {
      display: inline-block;
      margin-right: 20px;

      color: $color-text;
      font-size: 14px;
      font-family: $font-bold;
    }

    &:after {
      content: '';

      position: absolute;
      top: 50%;
      right: -10px;

      margin-top: -3px;

      transform: rotate(-45deg) translate3d(0, -50%, 0);
      transform-origin: center;

      width: 8px;
      height: 8px;

      // background-color: red;

      border-left: 2px solid $color-gray-button;
      border-bottom: 2px solid $color-gray-button;
    }
  }
}



