.flash {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 110;

  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  text-align: center;

  background-color: rgba(41, 38, 39, 0.9);

  &__content {
    position: relative;

    max-width: 410px;
    width: 100%;
    padding-top: 70px;
    padding-bottom: 50px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 20px;
    margin-right: 20px;

    border-bottom-right-radius: 14px;
    border-bottom-left-radius: 14px;
    background-color: #ffffff;

    h3 {
      margin: 0;
      margin-bottom: 20px;
    }

    p {
      margin: 0;
      margin-bottom: 30px;
    }

    &:before {
      content: '';

      position: absolute;
      top: -8px;
      left: 0;

      width: 100%;
      height: 8px;

      border-top-right-radius: 6px;
      border-top-left-radius: 6px;

      background-image: linear-gradient(to right, #ab89d6 0%, #f07397 100%);
    }
  }
}

@include laptop-s {
  .flash {
    &__content {
      padding-top: 75px;
      padding-left: 75px;
      padding-right: 75px;

      p {
        margin-bottom: 45px;
      }
    }
  }
}



