.message{
    &__head {
        display: flex;
        align-items: center;
    }

    &__body {

    }

    &__footer {
        display: flex;
    }

    &-title {
        line-height: (26/16)*1em;
        margin: 0;
    }

    &-replied {
        margin-left: 1em;
        color: $primary-light;
        font-size: (12/16)*1em;
        font-family: $font-bold;
        letter-spacing: 0.3px;
        transition: all 0.85s $default-timing;
        &:hover {
            color: $color-primary;
        }
    }

    &-date {
        color: $primary-light;
        padding-right: 1em;
    }
}


