.form {
  h3 {
    margin: 0;
    margin-bottom: 45px;

    text-align: center;
    text-transform: uppercase;
  }

  &--sign-up,
  &--sign-in {
    margin-top: 0;
  }

  // &--reset-password,
  // &--new-password {
  //   margin-top: 15%;
  // }

  &--reset-password {

    h3 {
      margin-bottom: 0px;
    }

    p {
      margin-top: 5px;
    }
  }

  .info-window__desc {
    margin-top: 25px;

    color: $color-headers;

    a {
      color: $color-headers;
    }
  }

  .input {
    margin-top: 30px;
  }

  &__link {
    font-size: 13px;
    color: $color-text;

    transition: all 0.5s $default-timing;

    &:hover {
      color: $color-primary;
    }

    &--above-form {
      position: absolute;
      bottom: 100%;
      left: 0;

      width: 100%;
      margin-bottom: 50px;
    }
  }

  &__social-list {
    margin-top: 20px;

    h6 {
      margin: 0;

      color: $color-text;
      font-size: 11px;
      letter-spacing: 0.2px;
    }

    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;

      list-style: none;

      li {
        a {
          display: inline-block;
          margin-right: 18px;

          &:hover {
            svg {
              fill: $color-primary;
            }
          }
        }

        &:last-of-type {
          a {
            margin-right: 0;
          }
        }

        svg {
          width: 14px;
          height: 14px;

          fill: $color-text;

          transition: all 0.5s $default-timing;
        }
      }
    }
  }

  &--auth {
    text-align: center;

    .input {
      &--type-box {
        width: auto;
        display: inline-block;
        margin-top: 20px !important;
      }
    }
  }

  &--centered {
    margin-bottom: 0;
  }
}

@include pocket {
  .form {
    &--reset-password {
      padding: 55px 50px;
    }

    .input {
      margin-top: 35px;
    }
  }
}

@include tablet {
  .form {
    &__social-list {
      margin-top: 40px;
    }
  }
}

@include laptop-s {
  .form {
    &__social-list {
      margin-top: 45px;
    }

    h3 {
      margin-bottom: 55px;
    }

    &--reset-password {
      padding: 80px;

      h3 {
        margin-bottom: 10px;
      }

      .input {
        margin-top: 60px;
      }
    }

    &--sign-up {
      .button {
        margin-top: 40px;
      }
    }
  }
}

@include laptop-m {
  .form {
    &--new-password {
      .button {
        margin-top: 35px;
      }
    }
  }
}

@include v-laptop-l {
  .form {
    h3 {
      margin-bottom: 65px;
    }

    &--reset-password {
      h3 {
        margin-bottom: 10px;
      }
    }
  }
}
.input--error {
  position: relative;
}


