.info-window {
  position: relative;

  max-width: 260px;
  padding: 25px 20px;
  margin: 0 auto;
  margin-top: 15%;
  margin-bottom: 60px;

  text-align: center;

  border-radius: 14px;
  background-color: #ffffff;

  box-shadow: 0 3px 45px 0 rgba(51, 62, 73, 0.1);

  .button {
    width: 100%;

    margin-top: 35px;

    justify-content: center;
  }

  &__icon {
    svg {
      width: 140px;
      height: 95px;
    }
  }

  &__desc {
    margin-top: 35px;
    margin-bottom: 0;

    color: $color-text;
    font-size: 11px;
    letter-spacing: 0.1px;
    text-align: center;

    a {
      color: $color-text;
      text-decoration: underline;
    }

    &--bottom {
      position: absolute;
      bottom: -45px;
      left: 0;

      width: 100%;
      margin: 0;

      color: $color-headers;
      text-align: center;

      a {
        color: $color-headers;

        // transition: all 0.5s $default-timing;
        //
        // &:hover {
        //   color: $color-primary;
        // }
      }
    }
  }
}

@include pocket {
  .info-window {
    max-width: 325px;
    padding: 40px 50px;
  }
}

@include laptop-s {
  .info-window {
    max-width: 400px;
    padding: 40px 80px;
  }
}



