label {
  display: block;

  color: $color-text;
  font-size: 13px;
  font-family: $font-regular;
  white-space: nowrap;

  transition: all 0.3s $default-timing;

  cursor: text;
}

.input {
  position: relative;

  width: 100%;

  transition: border-color 0.3s ease;

  &__group {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .input {
      min-width: 50%;
    }
  }

  &__group-separator {
    padding: 0 5px;

    color: $color-text;
    font-family: $font-bold;
  }

  &__error-message {
    position: absolute;
    top: 100%;
    left: 0;
    list-style: none;

    width: 100%;
    margin-top: 4px;

    color: #fc5c7d;
    font-size: 10px;
    text-align: center;
  }

  &-clear {
    display: block;
    width: 16px;
    height: 16px;

    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);

    cursor: pointer;

    svg {
      transition: fill .3s $default-timing;
      position: relative !important;
      left: inherit !important;

      transform: rotateZ(135deg);
    }

    &:hover {
      svg {
        fill: $color-primary;
      }
    }
  }

  input,
  textarea {
    width: 100%;
    // padding: 9px;
    padding: 5px;

    color: $color-headers;
    font-family: $font-bold;
    font-size: 15px;

    border: 0;
    background-color: #ffffff;

    transition: all 0.3s $default-timing;

    &.parsley-error {
      border-color: $color-secondary;
    }
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &--type-text {
    margin-top: 35px;
    border-radius: 4px;
    border: 1px solid $color-borders;

    transition: all 0.5s $default-timing;

    label {
      position: absolute;
      bottom: 100%;
      left: 0;

      margin-bottom: 8px;

      white-space: nowrap;
      text-overflow: ellipsis;
    }

    input {
      padding: 10px 15px;

      border-radius: 4px;

      font-size: 13px;

      background-color: transparent;

      &:focus {
        outline: 0;
      }
    }
  }

  &--type-textarea {
    textarea {
      width: 100%;
      max-height: 150px;
      padding: 8px 15px;

      resize: none;

      color: $color-text;
      font-family: $font-regular;
      font-size: 13px;
      line-height: 1.75;
      letter-spacing: 0.2px;

      &:focus {
        outline: 0;
      }
    }
  }

  &--icon {
    overflow: hidden;

    margin-top: 0;
    border-radius: 50px;

    svg {
      position: absolute;
      top: calc(50% - 8px);
      left: 20px;

      width: 16px;
      height: 16px;

      fill: #BFC7D2;
    }

    label {
      bottom: inherit;
      top: calc(50% - 7px);
      left: 55px;
    }

    input {
      padding-left: 55px;
    }
  }

  &--transparent {

    border: 0;
    border-bottom: 1px solid $color-headers;
    border-radius: 0;

    svg {
      top: calc(50% - 9px);
      left: 0;
      fill: $color-headers;
    }

    label {
      left: 30px;
      color: $color-headers;
    }

    input {
      height: 35px;

      padding: 0;
      padding-left: 30px;
      // padding-bottom: 10px;
    }
  }

  &--fluid {
    max-width: 195px;
  }

  &--type-select {
    margin-top: 12px;

    label {
      margin-bottom: 8px;
    }
  }

  &--type-slider {
    display: flex;
    align-items: center;

    span {
      color: $color-text;
      font-size: 15px;
    }

    input {
      max-width: 65px;
      margin: 0 9px;
    }
  }

  &--type-plus-minus {
    max-width: 100px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 13px;

    border: 1px solid $color-borders;
    border-radius: 6px;

    button {
      color: $color-text;
      font-family: $font-bold;

      border: 0;
      background-color: transparent;

      cursor: pointer;

      &:hover {
        color: $color-primary;
      }
    }

    input {
      border: 0;
      border-radius: 0;
      padding: 0;
      margin: 0 10px;

      color: $color-headers;
      font-family: $font-bold;
    }
  }

  &--type-double-handle-slider {
    max-width: 190px;
    margin-top: 30px;
    margin-left: 6px;
    margin-bottom: 5px;
  }

  &--type-box {
    display: inline-block;

    input {
      display: none;
    }

    &:hover {
      label {
        color: $color-headers;
      }
    }

    label {
      position: relative;

      display: inline-block;
      padding-left: 28px;
      margin-top: 20px;

      color: $color-headers;
      font-size: 14px;
      letter-spacing: 0.2px;
      font-family: $font-regular;
      line-height: 18px;

      cursor: pointer;

      transition: all 0.5s $default-timing;

      h4 {
        margin: 0;
      }

      p {
        margin: 0;

        font-size: 12px;
      }

      &:after,
      &:before {
        content: '';

        position: absolute;
        top: 1px;
        left: 0;

        width: 12px;
        height: 12px;

        transform-origin: center;
        transition: all 0.5s $default-timing;

        border-radius: 2px;
        border: 1px solid transparent;
      }

      &:after {
        transform: scale(1);
        border-color: $color-borders;
        background-color: $color-input-fill;
      }

      &:before {
        content: 'â';

        left: 1px;
        z-index: 2;

        color: #fc5c7d;
        text-align: center;
        line-height: 14px;

        transform: scale(0);
      }

      span {
        display: block;

        &:nth-child(1) {
          color: $color-headers;
          font-family: $font-bold;
        }

        &:nth-child(2) {
          color: $color-text;
          font-size: 12px;
        }
      }
    }

    input[type='radio'] + label {
      &:after,
      &:before {
        border-radius: 50%;
      }

      &:before {
        content: '';
        width: 12px;
        height: 12px;

        left: 0;

        background-color: #fc5c7d;
      }
    }

    input:checked + label {
      color: $color-headers;

      &:before {
        transform: scale(1);
      }
    }

    input[type='radio']:checked + label {
      &:before {
        transform: scale(0.5);
      }
    }

    input:disabled + label {
      color: $color-disabled;

      &:after,
      &:before {
        opacity: 0.4;
        filter: grayscale(1);
      }
    }
  }

  &--type-file {
    width: auto;
    display: inline-block;

    label {
      display: inline-flex;
      align-items: center;
      justify-content: center;

      cursor: pointer;

      &:hover {
        svg {
          fill: $color-primary;
        }
        span {
          color: $color-primary;
        }
      }

      span {
        color: $color-blue-text;
        font-family: $font-bold;
        letter-spacing: 0.2px;
        transition: all 0.4s $default-timing;
      }

      svg {
        min-width: 20px;
        width: 13px;
        height: 13px;

        margin-right: 10px;

        fill: $color-blue-text;

        transition: all 0.4s $default-timing;
      }
    }
    input {
      position: absolute;
      visibility: hidden;
      top: -1000px;
      left: -1000px;
    }

    &.input--in-message {

      position: absolute;
      top: calc(50% - 9px);
      right: 35px;

      label svg {
        height: 18px;
      }
    }
  }

  // &--type-text.hovered,
  // &--type-textarea.hovered {
  //   label {
  //     color: #3c474a;
  //     opacity: 0;
  //   }
  // }

  &--type-text.hovered {
    background-color: $color-input-fill;

    input,
    textarea {
      background-color: transparent;
    }
  }

  &--icon.hovered {
    label {
      opacity: 0;

      visibility: hidden;

      transform: translateX(-20px);
    }
  }

  &--transparent.hovered {
    border-color: transparent;
    border-bottom-color: $color-headers;
    background-color: transparent;

    input {
      padding-bottom: 8px;
    }
  }

  &--fluid.hovered {
    max-width: 250px;
  }

  &--error {
    border-color: #fc5c7d;
  }
}

.input-group {
  &--radio {
    display: flex;
    margin-top: 28px;

    .input {
      margin-top: 0;
    }
  }
}



