.audio {
  position: relative;

  margin: 30px 0;

  &__desc {
    position: absolute;
    top: 0;
    left: 60px;

    h4 {
      margin: 0;

      font-size: 15px;
      letter-spacing: 0.3px;
    }

    span {
      color: $color-text;
      font-size: 12px;
      font-family: $font-medium;
      letter-spacing: 0.3px;
    }
  }
}



