.section {
  &__layout {
    // min-width: 1024px;
    margin: 0 20px;
  }

  &--auth {
    position: relative;
    z-index: 2;
  }

  &--centered &__layout {
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &--waves {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 1;

    width: 100%;
    min-height: 500px;

    transform: translate3d(0, 0, 0);

    #waves,
    .layer,
    .wave {
      width: 100%;
      height: 100%;
    }

    .layer {
      position: absolute;
      bottom: 0;
    }

    .wave {
      background-size: 225%;
      background-repeat: no-repeat;
      background-position: bottom;

      transform: scale(1.1) translate3d(0, -25px, 0);
      animation: waves 12s infinite alternate-reverse
        cubic-bezier(0.4, 0.25, 0.15, 0.5);

      &--01 {
        background-image: url(/img/svg/auth-layer-01.svg);
        animation-delay: 0.25s;
      }

      &--02 {
        background-image: url(/img/svg/auth-layer-02.svg);
        animation-delay: 1s;
      }

      &--03 {
        background-image: url(/img/svg/auth-layer-03.svg);
        animation-delay: 0.75s;
      }

      &--04 {
        background-image: url(/img/svg/auth-layer-04.svg);
        animation-delay: 0.5s;
      }

      @keyframes waves {
        0% {
          transform: scale(1.1) translate3d(0, 0, 0);
        }

        50% {
          transform: scale(1.1) translate3d(0, 25px, 0);
        }

        100% {
          transform: scale(1.1) translate3d(0, -25px, 0);
        }
      }
    }
  }
}

@include tablet {
  .section {
    .wave {
      background-size: 100%;
    }
  }
}

@include laptop-s {
  .section {
    &__layout {
      max-width: 960px;
      margin: 0 auto;
    }
  }
}

@include laptop-m {
  .section {
    &__layout {
      max-width: 1140px;
    }
  }
}

@include v-laptop-l {
  .section {
  }
}



